form {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
      width: 90%;
      max-width: 300px;
      background-color: #fff;
      padding: 33px 40px;
      display: flex;
      align-content: center;
      flex-direction: column;
    }

    label {
      text-align: center;
      font-weight: bold;
      font-size: #1E1E1F;
      margin-bottom: 26px;
    }

    textarea {
      background-color: #F9FBFC;
      border: 2px, solid #202329;
      border-radius: 2px;
      box-shadow: 0px 5px 50px rgba(32, 35, 41, 0.25);
      color: #1E1E1F;
      font-size: 20px;
      padding: 12px;
      height: 96px;
      width: calc(100%-25px);
    }

    textarea::placeholder {
    color: #a5a5a5;
    font-family: inherit;
    font-weight: 400;
    }

    textarea:focus {
      outline-color: #61DAFA;
    }

    .TodoForm-buttonContainer {
      margin-top: 14px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      width: 100%;
    }

    .TodoForm-button {
      cursor: pointer;
      display: inline-block;
      font-size: 1.8rem;
      color: #202926;
      font-weight: 400;
      width: 12rem;
      height: 3.8rem;
      border-radius: 2px;
      border: none;
    }

    .TodoForm-button-add {
      background-color: #1c9e3d;
      box-shadow: 0px 5px 25px rgba(58, 109, 85, 0.5);
    }

    .TodoForm-button-cancel {
      background: transparent;
    }